import NetUtil from '../utils/NetUtil'


//开始获取数据
const requestPosts = path => {
  return {
    type: 'REQUEST_POSTS',
    path
  }
}

//获取数据成功
const receivePosts = (path, json) => {
  return {
        type: 'RECEIVE_POSTS',
        path ,
        json 
    }
}

// 页面初次渲染时获取数据
export const fetchPosts = (path, postData) => {
	let target = 'http://127.0.0.1:3000';
    let url = target + path;
    return dispatch => {
        dispatch(requestPosts(postData));
        return NetUtil.get(url, postData, json => dispatch(receivePosts(path, json)), errors => {
        	console.log(errors);
        })
    }
}

/*-------------手动获取数据，需区分数据来源------------------*/
//开始获取数据
const getDataStart = path => {
  return {
    type: 'GET_DATA_START',
    path
  }
}

//获取数据成功
const getDataSuccess = (path, json, success, name) => {
  return {
    type: 'GET_DATA_SUCCESS',
    path ,
    json ,
    success ,
    name
  }
}

//手动调用获取数据的aciton
export const getData = (path, postData, success, name) => {
    let url = target + path + Tool.paramType(postData);
    return dispatch => {
        dispatch(getDataStart(postData));
        return NetUtil.get(url, postData)
        .then(json => dispatch(getDataSuccess(path, json, success, name)));
    }
}
