import { Tabs, WhiteSpace, List, Badge, Button, ActivityIndicator } from 'antd-mobile';
import React from 'react';
import ReactDOM from 'react-dom';
import Tex from './renderer.js';

import *as action from '../Action/';
import {connect} from 'react-redux';

const TabPane = Tabs.TabPane;
const Item = List.Item;
const Brief = Item.Brief;

const server_url = "http://127.0.0.1:3000";

Date.prototype.Format = function (fmt) { //author: meizz 
    var o = {
        "M+": this.getMonth() + 1, //月份 
        "d+": this.getDate(), //日 
        "h+": this.getHours(), //小时 
        "m+": this.getMinutes(), //分 
        "s+": this.getSeconds(), //秒 
        "q+": Math.floor((this.getMonth() + 3) / 3), //季度 
        "S": this.getMilliseconds() //毫秒 
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}

class MyTest extends React.Component {
  constructor(props) { 
  	super(props);
  }

  componentDidMount(){
    this.loadTest();
  }

  loadTest(){
    var url = server_url + "/klmanager/getMyTest";
    //this.setState({isLoading: true});
    var student_id = '1';
    this.props.
    NetUtil.get(url, {student_id}).then((tests) => {
      var finish_test = [], not_finish_test = [];
      for(var i = 0; i < tests.length; i++){
        console.log(tests[i]);
        if(tests[i].test_state){
          finish_test.push(tests[i]);
        }else{
          not_finish_test.push(tests[i]);
        }
      }
      console.log(not_finish_test);
      this.setState({finish_test: finish_test, not_finish_test: not_finish_test});
    });
  }

  callback(key) {
  	console.log('onChange', key);
  }

  handleTabClick(key) {
  	console.log('onTabClick', key);
  }


  onStart(e, test_id){
    console.log(test_id);
    this.setState({isLoading: true});
    var url = server_url + '/klmanager/getExerciseByTest';
    NetUtil.get(url, {test_id}).then((exercises) => {
        this.setState({isLoading: false});
        if(exercises && exercises.length > 0){
          var history = this.props.history;
          var state = {test_log: [], reward: {score: 0}, exindex: 0, exercise: exercises};
          history.push("/mobile-test/Question", state);
        }else{
          alert("暂无测试题");
        }
    });
  }

  render() {
    var {isFetching, tests} = this.props;
    var finish_test = [], not_finish_test = [];
    if(tests){
      for(var i = 0; i < tests.length; i++){
        console.log(tests[i]);
        if(tests[i].test_state){
          finish_test.push(tests[i]);
        }else{
          not_finish_test.push(tests[i]);
        }
      }
    }

    var finish_item = finish_test.map((item,i) => {
        var time = item.enable_time;
        return(
          <Item arrow="horizontal" multipleLine extra={time} onClick={e => this.onStart(e, item.test_id)}>
            {item.test_name}
            <Brief>{item.teacher_id}</Brief>
          </Item>
      );
    });
    var not_finish_item = not_finish_test.map((item,i) => {
        console.log(item);

        var time = item.enable_time;
        return(
          <Item arrow="horizontal" multipleLine extra={time} onClick={e => this.onStart(e, item.test_id)}>
            {item.test_name}
            <Brief>{item.teacher_id}</Brief>
          </Item>
      );
    });
    return (
    <div>
      <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '5rem', backgroundColor: '#fff' }}>
          我的任务
      </div>
      <Tabs defaultActiveKey="1" onChange={this.callback} onTabClick={this.handleTabClick}>
      <TabPane tab="未完成" key="1">
        <ActivityIndicator toast animating={this.state.isLoading} />
        <List className="my-list">
          {not_finish_item}
        </List>
      </TabPane>
      <TabPane tab="完成" key="2">
        <ActivityIndicator toast animating={this.props.isLoading} />
      	<List className="my-list">
        	{finish_item}
      	</List>
      </TabPane>
      </Tabs>
        
      <WhiteSpace />

    </div>
    
    );
  }
}

export default connect(state => {
  return {
    tests: state.data, 
    isFetching: isFetching, 
  }
}, [action.fetchPosts])(MyTest);
