import Question from '../Component/Question.js'
import AnswerTest from '../Component/AnswerTest.js'
import Welcome from '../Component/Welcome.js'
import TestResult from '../Component/TestResult.js'
import MyTest from '../Component/my_test.js'
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom'

const AppRouter = () => {
  return (
    <Router>
      <div>
        <Route exact path="/mobile-test/" component={MyTest} />
        <Route path="/mobile-test/Question" component={Question} />
        <Route path="/mobile-test/AnswerTest" component={AnswerTest} />
        <Route path="/mobile-test/TestResult" component={TestResult} />
      </div>
    </Router>
    );
}

export default AppRouter;


