

const defaultlState = {data: {}, isFetching: false};
export const fetchData = (state = defaultlState , action = {}) => {
    switch(action.type){
        case 'REQUEST_POSTS':
            return {'isFetching': true};
        case 'RECEIVE_POSTS':
            return {'data':action.json,'isFetching':false};//返回一个新的state
        default:
            return state
    }
}

//手动获取数据
export const requestData = (state = {isFetching: false}, action = {}) => {
    switch(action.type){
        case 'GET_DATA_START':
            return {isFetching: true};
        case 'GET_DATA_SUCCESS':
            action.success(action.json);
            state[action.name] = action.json;
            return state;
        default:
            return state;
    }
}

//获取测试数据
export const requestExercise = (state = {isFetching: false, exindex: 0, test_log: [], reward: {score: 0}}, action = {}) => {
    switch(action.type){
        case 'GET_EXERCISE_START':
            return {isFetching: true};
        case 'GET_EXERCISE_SUCCESS':
            state.exercise = action.json;
            return state;
        default:
            return state;
    }
}



